"use client";

import React, { useState } from "react";
import Image from "next/image";
import { useParams } from "next/navigation";

interface Service {
  id: number;
  title: string;
  description: string;
  location: string;
  image: string;
  tags: string[];
  rating: number;
  reviews: number;
  mapQuery: string;
}

const dummyServices: Service[] = [
  {
    id: 1,
    title: "1. LOREM IPSUM",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent vitae libero venenatis, tristique justo.",
    location: "San Francisco, CA",
    image: "/dummy/service1.jpg",
    tags: ["Skin Care", "Salon Treatment"],
    rating: 4.5,
    reviews: 12,
    mapQuery: "1600+Amphitheatre+Parkway,+Mountain+View,+CA",
  },
  {
    id: 2,
    title: "2. LOREM IPSUM",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent vitae libero venenatis, tristique justo.",
    location: "Los Angeles, CA",
    image: "/dummy/service2.jpg",
    tags: ["Skin Care", "Nail Spa"],
    rating: 4.7,
    reviews: 10,
    mapQuery: "6801+Hollywood+Blvd,+Los+Angeles,+CA",
  },
  {
    id: 3,
    title: "3. LOREM IPSUM",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent vitae libero venenatis, tristique justo.",
    location: "New York, NY",
    image: "/dummy/service3.jpg",
    tags: ["Salon", "Hair Treatment"],
    rating: 4.2,
    reviews: 15,
    mapQuery: "Times+Square,+New+York,+NY",
  },
];

const ServiceCategoryPage: React.FC = () => {
  const params = useParams();
  const idParam = params?.id || "unknown";

  const [activeService, setActiveService] = useState<number | null>(null);

  const selectedService = dummyServices.find(
    (service) => service.id === activeService
  );

  return (
    <main className="bg-white text-black">
      {/* Banner */}
      <section className="relative w-full">
        <Image
          src="/Service/Banner.png"
          alt="Category Banner"
          width={1200}
          height={400}
          className="w-full object-cover"
        />
      </section>

      {/* Filters and Listings */}
      <section className="max-w-7xl mx-auto px-4 py-10 grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Service List */}
        <div className="lg:col-span-2 space-y-6">
          {/* Heading with id param and custom-blue */}
          <h2 className="text-xl md:text-2xl font-bold mb-4 heading custom-blue">
            services/{idParam} — Top Treatment Services Near San Francisco, California
          </h2>

          {dummyServices.map((service) => (
            <div
              key={service.id}
              onClick={() => setActiveService(service.id)}
              className={`cursor-pointer border rounded-md overflow-hidden shadow-sm transition-all duration-300 relative ${
                activeService === service.id ? "text-white" : "text-black"
              }`}
              style={
                activeService === service.id
                  ? {
                      backgroundImage:
                        "linear-gradient(216.65deg, rgba(0, 0, 0, 0.36) -10.96%, rgba(0, 0, 0, 0.87) 29.64%, #000000 70.76%), url('/Footer/footer-bg.png')",
                      backgroundSize: "cover",
                      backgroundPosition: "center",
                      backgroundRepeat: "no-repeat",
                    }
                  : {}
              }
            >
              <div className="grid grid-cols-3">
                <Image
                  src={service.image}
                  alt={service.title}
                  width={150}
                  height={150}
                  className="object-cover h-full w-full col-span-1"
                />
                <div className="col-span-2 p-4">
                  <h3 className="font-semibold text-lg mb-1">{service.title}</h3>
                  <p className="text-sm mb-2">{service.description}</p>
                  <div className="flex flex-wrap gap-2 text-xs">
                    {service.tags.map((tag) => (
                      <span
                        key={tag}
                        className="bg-gray-200 text-gray-800 px-2 py-1 rounded"
                      >
                        {tag}
                      </span>
                    ))}
                  </div>
                  <div className="mt-2 text-sm">
                    ⭐ {service.rating} ({service.reviews} Reviews)
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Embedded Map */}
        {activeService !== null && selectedService && (
          <div className="w-full h-96 sticky top-20 animate-fade-in rounded shadow overflow-hidden transition-opacity duration-500">
            <iframe
              src={`https://www.google.com/maps?q=${selectedService.mapQuery}&output=embed`}
              width="100%"
              height="100%"
              style={{ border: 0 }}
              allowFullScreen
              loading="lazy"
              className="rounded"
            />
          </div>
        )}
      </section>
    </main>
  );
};

export default ServiceCategoryPage;
